.media-text-overlay-wrapper {
    position: relative;
    overflow: hidden;

    @media (min-width: $breakpoint-lg) {
        position: relative;
        height: 100vh;
        z-index: 1;

        &.expand {
            height: 120vh;
        }

        .container {
            padding: 0 8rem;
        }
    }

    > picture {
        position: unset;
        display: block;
        overflow: hidden;
        height: 60vh;

        @media (min-width: $breakpoint-lg) {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: -1;
        }

        > img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    > .text-overlay {
        display: flex;
        height: 100%;
        align-items: center;

        @media (min-width: $breakpoint-lg) {
            &.position-top {
                align-items: flex-start;
                padding-top: 10rem;
            }
            &.position-bottom {
                align-items: flex-end;
                padding-bottom: 10rem;
            }
        }

        .content-wrapper {
            padding: 4rem 0;
            width: 100%;
            color: var(--color-black);
            
            @media (min-width: $breakpoint-lg) {
                width: 60%;
                color: var(--color-white);


                &.position-right {
                    margin-left: auto;
                }

                &.position-center {
                    margin: 0 auto;
                    text-align: center;

                    .actions {
                        justify-content: space-around;
                    }
                }
            }

            @media (min-width: $breakpoint-xl) {
                width: 45%;
            }
        }
    }
}
